.sidebar {
    flex: 1;
    border-right: 0.5px solid rgb(228, 228, 228);
    min-height: 100vh;
    background-color: white;
    font-family: sans-serif;
    letter-spacing: 1px;

    .top {
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;

        .logo {
            font-size: 20px;
            font-weight: bold;
            color: rgb(9, 102, 224);            
        }
    }

    hr {
        height: 0;
        border: 0.5px solid rgb(228, 228, 228);
    }

    .center {
        padding-left: 10px;

        ul {
            list-style: none;
            margin: 0;
            padding: 0;

            .title {
                font-size: 10px;
                font-weight: bold;
                color: #999;
                letter-spacing: 1px;
                margin-top: 15px;
                margin-bottom: 5px;
            }

            li {
                display: flex;
                align-items: center;
                padding: 5px;
                cursor: pointer;

                &:hover {
                    background-color: rgb(165, 194, 233);
                }

                .icon {
                    font-size: 18px;
                    color: rgb(9, 102, 224);
                }

                span {
                    font-size: 13px;
                    font-weight: 600;
                    color: #888;
                    margin-left: 10px;
                }
            }
        }
    }

    .bottom {
        display: flex;
        align-items: center;
        margin: 10px;

        .colorOption {
            width: 20px;
            height: 20px;
            border-radius: 5px;
            border: 1px solid #7451f8;
            cursor: pointer;
            margin: 5px;

            &:nth-child(1) {
                background-color: whitesmoke;
            }
            &:nth-child(2) {
                background-color: #333;
            }            
        }
    }

}