.navbar {
    height: 58px;
    display: flex;
    flex: 6;
    align-items: center;
    border-bottom: 0.5px solid rgb(231, 228, 228);
    font-size: 14px;
    color: #555;

    .wrapper {
        padding: 20px;
        display: flex;   
        align-items: center;
        justify-content: space-between;
        width: 100%;

        .search {
            display: flex;
            align-items: center;
            border: 0.5px solid lightgray;
            padding: 3px;
        }

        input {
            border: none;
            outline: none;
            background-color: transparent;

            &::placeholder {
                font-size: 12px;
            }
        }
        
        .items {
            align-items: center;
            display: flex;

            .item {
                display: flex;
                align-items: center;
                margin-right: 20px;
                position: relative;                

                .icon {
                    font-size: 20px;
                }

                .counter {
                    width: 15px;
                    height: 15px;
                    background-color: red;
                    border-radius: 50%;
                    color: white;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 10px;
                    font-weight: bold;   
                    position: absolute;
                    top: -5px;
                    right: -5px;                 
                }
            }
        }
    }
}